import React, { useState } from "react";
import { Code, Heading, Stack, Text } from "@chakra-ui/layout";
import { Box, Button, Input } from "@chakra-ui/react";
import { useMoralis } from "react-moralis";
import { CodeBlock } from "../components/CodeBlock";

export const User = () => {
  const { user, userError, isUserUpdating, setUserData } = useMoralis();

  const [email, setEmail] = useState(user.email);
  const [password, setPassword] = useState("");
  const [phone, setPhone] = useState("");
  const [username, setUsername] = useState("");

  const updateUser = (event: React.FormEvent) => {
    event.preventDefault();

    console.log("update user");
    setUserData({ email, password, phone, username });
  };

  return (
    <div>
      <Stack spacing={6}>
        <Heading>User</Heading>
        <Box maxWidth={500}>
          <form onSubmit={updateUser}>
            <Stack spacing={6}>
              <Input
                value={email}
                onChange={(e) => setEmail(e.currentTarget.value)}
                placeholder="email"
              />
              <Input
                value={username}
                onChange={(e) => setUsername(e.currentTarget.value)}
                placeholder="username"
              />
              <Input
                type="password"
                value={password}
                onChange={(e) => setPassword(e.currentTarget.value)}
                placeholder="password"
              />
              <Input
                type="phone"
                value={phone}
                onChange={(e) => setPhone(e.currentTarget.value)}
                placeholder="phone"
              />
              <Button
                type="submit"
                colorScheme="green"
                disabled={isUserUpdating}
              >
                Update user
              </Button>
            </Stack>
          </form>
        </Box>
        <CodeBlock>
          {JSON.stringify(
            {
              user,
              userError: {
                name: userError && userError.name,
                message: userError && userError.message,
              },
              isUserUpdating,
            },
            null,
            2
          )}
        </CodeBlock>
      </Stack>
    </div>
  );
};
