import { Badge, Box, Container, Heading, Stack } from "@chakra-ui/react";
import { Link, Redirect, Route, Switch, useLocation } from "react-router-dom";
import React from "react";
import { menu } from "./menu";

function App() {
  const location = useLocation();
  return (
    <Container my={8} maxW="container.lg">
      <Heading as="h1" size="4xl">
        react-moralis examples
      </Heading>
      <Box mt={6} mb={12}>
        <Stack direction="row">
          {menu.map(({ label, path }) => (
            <Badge
              as={Link}
              to={path}
              colorScheme="green"
              ml="1"
              fontSize="1.1em"
              variant={location.pathname === path ? "solid" : "subtle"}
            >
              {label}
            </Badge>
          ))}
        </Stack>
      </Box>
      <Box>
        <Switch>
          {menu.map(({ path, component: Component }) => (
            <Route key={path} path={path} exact>
              <Component />
            </Route>
          ))}
          <Route path="*">
            <Redirect to={menu[0].path} />
          </Route>
        </Switch>
      </Box>
    </Container>
  );
}

export default App;
